import React, { lazy } from 'react';
import { Link } from 'react-router-dom';
import { Panel, PanelHeader } from '../../../components/panel/panel.jsx';
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search, CSVExport } from 'react-bootstrap-table2-toolkit';
import paginationFactory from 'react-bootstrap-table2-paginator';
import { ShowtModalKategori, showModal } from '../../../actions/datamaster_action.jsx';
import { connect } from 'react-redux';
import Swal from 'sweetalert2';
import { NotifSucces } from "../../../components/helpers/notification.jsx";
const FormModalDataKategori = lazy(() => import( './FormModalDataKategori.jsx'));

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
    return {
        hideModal: state.datamaster.modalDialog
    }
}
const hapusDataKategori = (params, dispatch) => {
  console.log(params);
    Swal.fire({
        html:
        'Apakah Anda Yakin Ingin ' +
        'Menghapus ' +
        '<h1><b>Kode '+params+'</b> ?</h1>',
        icon: 'warning',
        position: 'top-center',
        cancelButtonText: 'Tidak',
        showCancelButton: true,
        confirmButtonText: 'OK',
        showConfirmButton: true,
    }).then((result) => {
        if (result.isConfirmed) {
            NotifSucces('Data Berhasil Di Hapus')
        }
    })

}
class MasterKategori extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          isEdit: false,
          modalDialog: false,
          isLoading: false,
          columns: [
            {
              dataField: "kode",
              text: "Kode",
              sort: true,
            },
            {
              dataField: "nama_kategori",
              text: "Nama Kategori",
            },
            {
              dataField: "harga_pergram",
              text: "Harga / Gram",
              formatter: (cell) => {
                return (
                  <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
                );
              },
            },

            {
              dataField: "presentase",
              text: "Persentase",
              formatter: (cell) => {
                return cell.toFixed(2);
              },
            },
            {
              dataField: "kadar",
              text: "Kadar",
            },
            {
              dataField: "action",
              text: "Action",
              csvExport: false,
              headerClasses: "text-center",
              formatter: (rowcontent, row) => {
                let dataEdit = {
                  kode: row.kode,
                  nama_kategori: row.nama_kategori,
                  harga_pergram: row.harga_pergram,
                  presentase: row.presentase,
                  kadar: row.kadar,
                };
                 
                return (
                  <div className="row text-center">
                    <div className="col-12">
                      <button
                        onClick={() => this.formEditDataKategori(dataEdit)}
                        className="btn btn-primary mr-3"
                      >  <i className="fa fa-edit"></i>             
                      </button>
                      <button
                        onClick={() =>
                          hapusDataKategori(
                            row.kode,
                            this.props.dispatch
                          )
                        }
                        className="btn btn-danger"
                      >
                        <i className="fa fa-trash"></i>  
                      </button>
                    </div>
                  </div>
                );
              },
            },
          ],
          datakategori: [
            {
              kode: "CC",
              nama_kategori: "Emas Tua",
              harga_pergram: "7700000",
              presentase: "80",
              kadar: "17k",
            },
          ],
        };
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(data) {
        this.setState({ isLoading: true });
        console.log(data);
    }
    formEditDataKategori(dataEdit) {
        this.props.dispatch(showModal())
        this.props.dispatch(ShowtModalKategori(dataEdit));
        this.setState({
          isLoading: false,
        });
    }
    showModalKategori() {
        this.props.dispatch(showModal());
        this.props.dispatch(ShowtModalKategori(false))
        this.setState({
          isEdit: false,
          isLoading: false,
        });
    }
    render() {
		return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Kategori</li>
        </ol>
        <h1 className="page-header">Master Kategori </h1>
        <Panel>
          <PanelHeader>Master Kategori</PanelHeader>
          <br />

          {/* Master Kategori */}
          <div className="container">
            <ToolkitProvider
              keyField="kode"
              data={this.state.datakategori || []}
              columns={this.state.columns}
              search
              exportCSV={{
                fileName: "Export Master Kategori.csv",
              }}
            >
              {(props) => (
                <div className="row">
                  <div className="col-6">
                    <button
                      onClick={() => this.showModalKategori()}
                      className="btn btn-primary"
                      data-tip="Tambah Data"
                    >
                      <i className="fa fa-plus"></i>
                    </button>
                  </div>
                  <div className="col-6">
                    <div className="text-right">
                      <SearchBar {...props.searchProps} />
                    </div>
                  </div>
                  <hr />
                  <div className="col-12">
                    <BootstrapTable
                      pagination={paginationFactory()}
                      {...props.baseProps}
                    />
                    <br />
                    <ExportCSVButton {...props.csvProps}>
                      Export CSV!! 
                    </ExportCSVButton>
                  </div>
                </div>
              )}
            </ToolkitProvider>
          </div>
          <br />
          {/* End Master Kategori */}
          <FormModalDataKategori
            isEdit={this.state.isEdit}
            isLoading={this.state.isLoading}
            title={
              this.state.isEdit ? "Edit Data Kategori" : "Tambah Data Kategori"
            }
            isOpen={this.props.hideModal}
            onSubmit={(data) => this.handleSubmit(data)}
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Data Kategori' : 'Tambah Data Kategori' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Kategori  */}
        </Panel>
      </div>
    );
	}
}


export default connect(maptostate, null)(MasterKategori);